import styles from './machineSelect.module.css';
import { useState } from 'react';
import SearchForm from '../SearchForm/SearchForm';

const MachineSelect = () => {
  const [model, setModel] = useState<number | null>(null);

  return (
    <>
      {!model &&
        <div className={styles.select}>
          <button onClick={() => setModel(14000)} className={styles.button}>
            C14000
          </button>
          <button onClick={() => setModel(7100)} className={styles.button}>
            C7100
          </button>
          <button onClick={() => setModel(6100)} className={styles.button}>
            C6100
          </button>
          <button onClick={() => setModel(4080)} className={styles.button}>
            C4080
          </button>
          <button onClick={() => setModel(3080)} className={styles.button}>
            C3080
          </button>
        </div>
      }
      {model && <SearchForm model={model} />}
    </>
  );
};

export default MachineSelect;
