import { useState } from 'react';
import { makeAutoObservable } from "mobx"
import { c6100Base } from "./base"
import { array } from './object';

class Errors {
    base: any  = null


     constructor() {
        makeAutoObservable(this)
    }

    findError(value: any) {
        this.base = c6100Base.filter((item) => {
                return item.Code === ` C-${value}`
        })
    }
}

export default new Errors()