import styles from './app.module.css';
import { useState } from 'react';
import MachineSelect from './components/MachineSelect/MachineSelect';
import SaveBase from './helpers/baseSave';

function App() {
  const [toggle, setToggle] = useState<boolean | null>(null);
  const rrr = 'dsfsxdfrjytjuf'

  return (
    <div className={styles.app}>
      {toggle == null && (
        <>
          <button onClick={() => setToggle(false)} className={styles.button}>Select Machine</button>
          <button onClick={() => setToggle(true)} className={styles.button}>FaQ</button>
        </>
      )}
      {toggle == false && <MachineSelect />}
      <SaveBase />
    </div>
  );
}

export default App;

